import * as React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import ButtonBase from '@mui/material/ButtonBase';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import {  NavLink } from 'react-router-dom';
import urlimage from '../images/SUNY1245.jpg'
import ReceptionGown from '../images/SWAS8684.JPG'
import Indowestern from '../images/IMG_20200829_145319_934.jpg'


const images = [
  {
    url: urlimage,
    title: 'Bridal lehengas',
    width: '33.33%',
  },
  {
    url: ReceptionGown,
    title: 'Reception Gown',
    width: '33.33%',
  },
  {
    url: Indowestern,
    title: 'Indo-western',
    width: '33.33%',
  },
];

const ImageButton = styled(ButtonBase)(({ theme }) => ({
  position: 'relative',
  height: 600,
  [theme.breakpoints.down('sm')]: {
    width: '100% !important', // Overrides inline-style
    height: 300,
  },
  '&:hover, &.Mui-focusVisible': {
    zIndex: 1,
    '& .MuiImageBackdrop-root': {
      opacity: 0.15,
    },
    '& .MuiImageMarked-root': {
      opacity: 0,
    },
    '& .MuiTypography-root': {
      border: '4px solid currentColor',
    },
  },
}));

const ImageSrc = styled('span')({
  position: 'absolute',
  left: 0,
  right: 0,
  top: 0,
  bottom: 0,
  backgroundSize: 'cover',
  backgroundPosition: 'center 40%',
});

const Image = styled('span')(({ theme }) => ({
  position: 'absolute',
  left: 0,
  right: 0,
  top: 0,
  bottom: 0,
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  color: theme.palette.common.white,
}));

const ImageBackdrop = styled('span')(({ theme }) => ({
  position: 'absolute',
  left: 0,
  right: 0,
  top: 0,
  bottom: 0,
  backgroundColor: theme.palette.common.black,
  opacity: 0.4,
  transition: theme.transitions.create('opacity'),
}));

const ImageMarked = styled('span')(({ theme }) => ({
  height: 3,
  width: 18,
  backgroundColor: theme.palette.common.white,
  position: 'absolute',
  bottom: -2,
  left: 'calc(50% - 9px)',
  transition: theme.transitions.create('opacity'),
}));

export default function WomensCollection() {
  return (
    <>
<Box
      
        sx={{
            display:{xs:'none',md:'block'},
            width: '100%',
            height: 400,
            backgroundColor: 'white',
            }}
    >
    </Box>
    <Box
      
        sx={{
            display:{xs:'block',md:'none'},
            width: '100%',
            height: 510,
            backgroundColor: 'white',
            }}

    >
    </Box>
    <Paper id='womenscollectionbig' elevation={0} sx={{ display:{xs:'none',md:'block'},height:'110px',backgroundColor:'inherit',textAlign:'center',overflow:'hidden'}}>
        <Typography sx={{fontFamily:'monospace',fontSize:'30px',marginBotton:'20px'}}>WomensCollection </Typography>
        <Typography  sx={{fontFamily:'monospace',fontSize:'15px',marginBotton:'20px'}}>Year 2022</Typography>
        <Typography  sx={{fontFamily:'monospace',fontSize:'12px',marginBotton:'20px'}}>Suiting all sizes of budgets, Diva creations offers some of the best collections of best bridal lehengas, shararas, ghagra-cholis, salwar kameezes and more.</Typography>
   </Paper>
   <Paper id='womenscollection' elevation={0} sx={{ display:{xs:'block',md:'none'},height:'100px',backgroundColor:'inherit',textAlign:'center',overflow:'hidden'}}>
        <Typography sx={{fontFamily:'monospace',fontSize:'25px',marginBotton:'15px'}}>WomensCollection </Typography>
        <Typography  sx={{fontFamily:'monospace',fontSize:'12px',marginBotton:'15px'}}>Year 2022</Typography>
        <Typography  sx={{fontFamily:'monospace',fontSize:'10px',marginBotton:'15px'}}>Suiting all sizes of budgets, Diva creations offers some of the best collections of best bridal lehengas, shararas, ghagra-cholis, salwar kameezes and more.</Typography>
   </Paper>

    
    <Box  sx={{ display: 'flex', flexWrap: 'wrap', minWidth: 300, width: '100%' }} >
      {images.map((image) => (
        <ImageButton
          focusRipple
          key={image.title}
          style={{
            width: image.width,
          }}
        >
          <ImageSrc style={{ backgroundImage: `url(${image.url})` }} />
          <ImageBackdrop className="MuiImageBackdrop-root" />
          <Image>
            <Typography
              component="span"
              variant="subtitle1"
              color="inherit"
              sx={{
                position: 'relative',
                p: 4,
                pt: 2,
                pb: (theme) => `calc(${theme.spacing(1)} + 6px)`,
              }}
            >
              <NavLink to='/womenscollection' style={{color:'white',textDecoration:'none'}}>{image.title}</NavLink> 
              <ImageMarked className="MuiImageMarked-root" />
            </Typography>
          </Image>
        </ImageButton>
      ))}
    </Box>
    </>
  );
}
