import * as React from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import DrawerPresistent from '../navbar/DrawerPresistent'
import Footer from '../Footer/Footer';
import Paper from '@mui/material/Paper';
import { Button, Typography } from '@mui/material';
import { useState } from 'react';


export default function BasicTextFields() {
    const [name,setName]=useState('')
    const [contact,setContact]=useState('')
    const [email,setEmail]=useState('')
    const [enquiry,setEnquiry]=useState('')


    const clickhandler =()=>{
 
        fetch('https://diva-website.herokuapp.com/insert',{
        method :'POST',
        headers :{
            'Accept':'application/json',
            'Content-Type':'application/json'
          },
        body :JSON.stringify({
            Name:name,
            Contact:contact, 
            Email:email,
            Enquiry:enquiry,
        })
          }).then(response=>response.json())
          .then(data=>console.log(data))

       
        console.log(name)
        console.log(contact)
        console.log(email)
        console.log(enquiry)

    }

    
  return (
    <>
    <Box
      component="form"
      sx={{width:'100%',
           backgroundImage:`url( 'https://images.unsplash.com/photo-1525097487452-6278ff080c31')`,
           backgroundSize:'contain',
           height:'600px',
           display:'flex',
           flexGrow:1,
           justifyContent:'center',
           alignItems:'center'
    }}
      noValidate
      autoComplete="off"
    >
      <DrawerPresistent />
       <Box 
       sx={{
        marginTop:'100px',
        width:'50%',
        height:'500px',
        borderRadius:'15px',
        background:'transparent'

}}
       >
       <Paper
       sx={{
        height:'40px',
        textAlign:'center',
        padding:'5px',
        fontSize:'1rem',
        fontWeight:'bold',
        background:'white'
       }}
       >BOOK YOUR APPOINTMENT  </Paper>
      <Typography sx={{padding:'5px',textAlign:'center',color:'white'}} >NAME</Typography>
      <TextField  onChange={(event)=>{setName(event.target.value)}} sx={{padding:'10px',backgroundColor:'white',borderRadius:'5px'}} size="small" fullWidth label="name" id="fullWidth"  />
      <Typography sx={{padding:'5px',textAlign:'center',color:'white'}}>CONTACT-NO</Typography>
      <TextField  onChange={(event)=>{setContact(event.target.value)}}  sx={{padding:'10px',backgroundColor:'white',borderRadius:'5px'}} size="small" fullWidth label="contact-no" id="fullWidth" />
      <Typography sx={{padding:'5px',textAlign:'center',color:'white'}}>EMAIL-ADDRESS</Typography>
      <TextField  onChange={(event)=>{setEmail(event.target.value)}}  sx={{padding:'10px',backgroundColor:'white',borderRadius:'5px'}} size="small" fullWidth label="email" id="fullWidth" />
      <Typography sx={{padding:'5px',textAlign:'center',color:'white'}}>ENQUIRY</Typography>
      <TextField
          onChange={(event)=>{setEnquiry(event.target.value)}}
          sx={{padding:'10px',backgroundColor:'white',borderRadius:'5px',width:'100%'}}
          id="outlined-textarea"
          label="enquiry"
          placeholder="enquiry"
          multiline
         
        />
            <Button  onClick={clickhandler} sx={{ background :'lightgrey',marginTop:'10px',paddingLeft:'45%',paddingRight:'45%' , color:'white'}}>Submit</Button>

               </Box>
      
    </Box>
    <Footer/>

    </>
  );
}
