import React from 'react'
import Footer from '../Footer/Footer'
import DrawerPersistent from '../navbar/DrawerPresistent'
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';


function Contact() {
  return (
    <div>
     
     <Box sx={{ width: '100%' ,background:'lightgrey'}}>
     <DrawerPersistent/>
     <Box sx={{display:'flex' ,flexGrow:'1',flexDirection:'column'}}>
     <Box sx={{height:'500px',width:'80%',marginInline:'auto',borderRadius:'20px',background:'transparent',justifyContent:'center',alignItems:'center'}}>
     <Typography variant="h2" gutterBottom sx={{textAlign:'center',fontFamily:'monospace'}}>
        operating and Registered Address
      </Typography>
      <Box  sx={{height:'400px',width:'80%',marginInline:'auto',overflow:'auto'}}>
        <Typography sx={{textAlign:'center',fontFamily:'monospace',fontSize:'1.5rem',flexWrap:'wrap'}}>
        Diva creation 
        shop no.2a, building no.312,
        kalbadevi main road,
        between swadesi and kakad market,
        mumbai-400002 

        </Typography>
  

      </Box>
     </Box>
    
    
    </Box>
   
    </Box>
    <Footer/>
    </div>
  )
}

export default Contact
