import { Box, Button, Paper } from '@mui/material'
import React, { useContext } from 'react'
import Footer from '../Footer/Footer'
import DrawerPresistent from '../navbar/DrawerPresistent'
import Cookies from "universal-cookie";
import { Navigate, useLocation, useNavigate } from 'react-router-dom';
import { Context } from '../../Context';


const cookies = new Cookies();
const token = cookies.get("TOKEN");


function Profile() {
  const {globaldata,setGlobaldata}=useContext(Context)
   
  console.log(globaldata)
    const location = useLocation()
    const navigate = useNavigate()
    const logout = () => {
        // destroy the cookie
        cookies.remove("TOKEN", { path: "/" });
        // redirect user to the landing page
        navigate('/')
    }
    if(token){
        return (
            <div style={{background:"grey"}}>
                <DrawerPresistent/>
                <Box sx={{display:'flex',flexDirection:'column',flexGrow:1}}>
                   <Paper sx={{textAlign:'center',background:'lightgrey'}}>
                   <h1>welcome </h1>
                   <h3>Your email address  {globaldata.email || globaldata.message }  </h3>
                   <p>
                    welcome {} you can book your appointment for ordering outfit on the home page
                     </p>
                     <p>
                    get the latest trend update by updating your email or just by signing up with diva creation
                     </p>
                 <Button onClick={logout}>Logout</Button>
                    </Paper> 
                  
                 
                </Box>
                <Footer/>
            </div>
          )
    }else{
        return <Navigate to='/' state={{path:location.pathname}}/>
        }
 
}

export default Profile