import React, { useState } from 'react'
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

import {  Typography } from '@mui/material';




function Registration() {

    //user registration 
const [user,setUser]=useState({
    Email:'',
    Password:'',
  })
  const clickhandler =()=>{
    fetch('https://diva-website.herokuapp.com/register',{
      method :'POST',
      headers :{
          'Accept':'application/json',
          'Content-Type':'application/json'
        },
      body :JSON.stringify({
          email:user.Email,
          password:user.Password
      })
        }).then(response=>response.json())
        .then(data=>console.log(data))
  
  
  
  
    console.log(user.Email)
    console.log(user.Password)
  
  }
  
  
  return (
    <div>
      <Box 
       sx={{

        width:'100%',
        height:'450px',
        padding:'25px',
        borderRadius:'15px',
        marginBottom:'50px'
}}
       >
        <Typography sx={{
        textAlign:'center',
        padding:'5px',
        fontSize:'1rem',
        fontWeight:'bold'}} >REGSISTRATION</Typography>
                <Box  sx={{ border: '2px dashed grey',margin:'20px',padding:'20px',borderRadius:'15px'}}>

      <Typography sx={{padding:'5px',textAlign:'center'}} >EMAIL-ADDRESS</Typography>
      <TextField  sx={{padding:'10px',backgroundColor:'white',borderRadius:'5px'}} onChange={(e)=>setUser({...user,Email:e.target.value })} size="small" fullWidth label="email" id="fullWidth" />
      <Typography sx={{padding:'5px',textAlign:'center'}}>SET PASSWORD</Typography>
      <TextField  sx={{padding:'10px',backgroundColor:'white',borderRadius:'5px'}} onChange={(e)=>setUser({...user,Password:e.target.value })} size="small" fullWidth label="password" id="fullWidth" />
      <Button variant="outlined" sx={{width:'100%',margin:'10px'}} onClick={clickhandler}>SUBMIT</Button>
                 </Box>
               </Box> 
    </div>
  )
}

export default Registration
