import React from 'react'
import PageLogo from '../main-content/PageLogo'
import Swipeable from '../main-content/Swipeable'
import CollectionTab from '../navbar/CollectionTab'
import DrawerPresistent from '../navbar/DrawerPresistent'
import WomensCollection from '../Collections/WomensCollection'
import MensCollection from '../Collections/MensCollection'
import Footer from '../Footer/Footer'


function Home() {
  return (
    <div>
        {/* navbar components */}
        <DrawerPresistent/>
       <Swipeable/>
       <CollectionTab/>
       <PageLogo/>
       <WomensCollection/>
       <MensCollection/>
       <Footer/>

    </div>
  )
}

export default Home
