import * as React from 'react';
import { styled } from '@mui/material/styles';
import MuiGrid from '@mui/material/Grid';
import Divider from '@mui/material/Divider';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import InstagramIcon from '@mui/icons-material/Instagram';
import FacebookIcon from '@mui/icons-material/Facebook';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import Link from '@mui/material/Link';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import { NavLink } from 'react-router-dom';
import CollectionTab from '../navbar/CollectionTab';




const Grid = styled(MuiGrid)(({ theme }) => ({
  width: '100%',
  ...theme.typography.body2,
  '& [role="separator"]': {
    margin: theme.spacing(0, 2),
  },
}));

export default function Footer() {
 

  return (
    <Box sx={{flexGrow:1,height:300}}>
      <Paper elevation={0}sx={{height:'50px'}}></Paper>
    <Grid container>
      <Grid item xs sx={{paddingLeft:'25px'}}>
       <h3> ABOUT US</h3>
       <NavLink to='/about-us'style={{display:'block',margin:'5px',textTransform:'capitalize',textDecoration:'none',color:'black'}} >
        About us 
       </NavLink>
       <NavLink to='/about-us'style={{display:'block',margin:'5px',textTransform:'capitalize',textDecoration:'none',color:'black'}} >
        our products and brand 
       </NavLink>
     
     
      </Grid>
      <Divider orientation="vertical" flexItem sx={{height:'200px'}}>
      </Divider>
      <Grid item xs sx={{paddingLeft:'25px'}}>
      <h3> ACCOUNT</h3>
      <Button
         component="button"
          variant="body2"   
          sx={{display:'flex',background:'lightgrey',color:'black',textDecoration:'none',font:'monospace',fontSize:'1.5rem',border:'1px solid white',borderRadius:'5px'}}
        >
          <CollectionTab Tabname="PROFILE" />
            My account  
        </Button>

      
            </Grid>
      <Divider orientation="vertical" flexItem>
      </Divider>

      <Grid item xs sx={{paddingLeft:'25px'}}>
      <h3>CONTACT US</h3>
       <NavLink to='/contact-us' style={{display:'block',margin:'5px',textDecoration:'none',color:'black',fontSize:'1rem'}}>
        {'contact-us'}
      </NavLink>
      
     
      </Grid>
      <Divider orientation="vertical" flexItem>
      </Divider>
      <Grid item xs sx={{paddingLeft:'15px'}}>
      <h4> SIGN UP TO RECEIVE EMAIL UPDATES ON SPECIAL PROMOTIONS, NEW PRODUCT ANNOUNCEMENTS, GIFTS IDEAS AND MORE.</h4>
      <TextField id="outlined-basic" label="ENTER EMAIL ADDRESS" variant="outlined" />
      <Button variant="outlined"  sx={{display:'block',height:'40px',textAlign:'center',paddingLeft:'83px',paddingRight:'83px'}}>Submit</Button>
      </Grid>
      
    </Grid>
    <Paper elevation={0}sx={{height:'50px'}}>
    </Paper>
    <Paper elevation={0}sx={{height:'50px',textAlign:'center'}}>
    <Divider/>
    <Link href="https://www.instagram.com/divacreation47/"><InstagramIcon sx={{fontSize:'40px',margin:'5px'}}/></Link>
     <Link href="https://www.facebook.com/divacreation47/"><FacebookIcon sx={{fontSize:'40px',margin:'5px'}}/></Link>
     <WhatsAppIcon sx={{fontSize:'40px',margin:'5px'}}/>
    </Paper>
      <Divider/>
    <Paper elevation={0}sx={{height:'30px',textAlign:'center',margin:'5px'}}>
      @Copyright 2022 Diva Creation media LLC
    </Paper>

    </Box>
  );
}
