import React, {  useContext, useState } from 'react'
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import {  Typography } from '@mui/material';
import Cookies from "universal-cookie";
import { useNavigate } from 'react-router-dom';
import { Context } from '../../Context';


const cookies = new Cookies();
const token = cookies.get("TOKEN");

function Login() {
const navigate = useNavigate()

const {globaldata,setGlobaldata}=useContext(Context)
 
//user login 
const [loginuser,setLoginuser]=useState({
    userid:'',
    loginpassword:'',
  })
  
  const loginclickhandler = () =>{
    fetch('https://diva-website.herokuapp.com/login',{
      method :'POST',
      headers :{
          'Accept':'application/json',
          'Content-Type':'application/json'
        },
      body :JSON.stringify({
          email:loginuser.userid,
          password:loginuser.loginpassword,
      })
        }).then(response=>response.json())
        .then(data=>{
          cookies.set("TOKEN", data.token, {
            path: "/",
          });
          if(token){
            navigate('/profile')
            setGlobaldata(data)
            
            }else{
              navigate('/')
            }
        
          
        })
       
  }
  

  
  
 

  return (
    <div>
      <Box 
       sx={{
        width:'100%',
        height:'450px',
        padding:'50px',
        marginBottom:'50px',
        borderRight:'2px solid black',
      
}}
       >
      <Typography sx={{
        textAlign:'center',
        padding:'5px',
        fontSize:'1rem',
        fontWeight:'bold',}} >LOGIN</Typography>
        <Box  sx={{ border: '2px dashed grey',margin:'20px',padding:'20px',borderRadius:'15px'}}>
       
      <Typography sx={{padding:'5px',textAlign:'center'}} >USERNAME</Typography>
      <TextField  sx={{padding:'10px',backgroundColor:'white',borderRadius:'5px'}} onChange={(e)=>{setLoginuser({...loginuser,userid:e.target.value})}} size="small" fullWidth label="username" id="fullWidth"  />
      <Typography sx={{padding:'5px',textAlign:'center'}}>PASSWORD</Typography>
      <TextField  sx={{padding:'10px',backgroundColor:'white',borderRadius:'5px'}}  onChange={(e)=>{setLoginuser({...loginuser,loginpassword:e.target.value})}} size="small" fullWidth label="password" id="fullWidth" />
      <Link sx={{ display:'block',padding:'5px',textAlign:'center'}}>Forget password?</Link>
      <Button variant="outlined" sx={{width:'100%'}} onClick={loginclickhandler}>SUBMIT</Button>
      </Box>
      
               </Box>  
         
    </div>

  )
}

export default Login

