import * as React from 'react';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Collapse from '@mui/material/Collapse';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import SendIcon from '@mui/icons-material/Send';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import { NavLink } from 'react-router-dom';
import MailIcon from '@mui/icons-material/Mail';
import ListItem from '@mui/material/ListItem';


export default function DrawerTab({Drawername}) {
  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(!open);
  };

  const Tabs=(Drawername)=>{
    switch(Drawername){
        case 'HOME':
            return(
                <ListItemButton>
                <ListItemIcon style={{color:'white',textDecoration:'none'}}>
                <SendIcon />
                </ListItemIcon>
                <NavLink style={{color:'white',textDecoration:'none'}} to="/">{Drawername}</NavLink>
                </ListItemButton>
            )
            case 'COLLECTION':
            return (
              <List>
                <ListItemButton onClick={handleClick} >
                  <ListItemIcon>
                    <InboxIcon style={{color:'white',textDecoration:'none'}}/>
                    
                  </ListItemIcon>
                  <ListItemText primary={Drawername} style={{color:'white',textDecoration:'none'}}/>
                  {open ? <ExpandLess style={{color:'white',textDecoration:'none'}} /> : <ExpandMore style={{color:'white',textDecoration:'none'}}/>}
                </ListItemButton>
                <Collapse in={open} timeout="auto" unmountOnExit>
                  <List>
                    {["MENS-COLLECTION", "WOMENS-COLLECTION"].map(
                      (text, index) => (
                        <ListItem key={text} disablePadding>
                          <ListItemButton>
                            <ListItemIcon style={{color:'white',textDecoration:'none'}}>
                              {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
                            </ListItemIcon>
                            {text === "MENS-COLLECTION" ? (
                              <NavLink to="/menscollection" style={{color:'white',textDecoration:'none'}}>{text}</NavLink>
                            ) : (
                              <NavLink to="/womenscollection" style={{color:'white',textDecoration:'none'}}>{text}</NavLink>
                            )}
                          </ListItemButton>
                        </ListItem>
                      )
                    )}
                  </List>
                </Collapse>
              </List>
            );

            case 'CONSULTANCY':
            return(
                <ListItemButton>
                <ListItemIcon style={{color:'white',textDecoration:'none'}}>
                <SendIcon />
                </ListItemIcon>              
                <NavLink to="/consultancy" style={{color:'white',textDecoration:'none'}}>{Drawername}</NavLink>
                </ListItemButton>
            )
            case 'CONTACT-US':
            return(
                <ListItemButton>
                <ListItemIcon style={{color:'white',textDecoration:'none'}}>
                <SendIcon />
                </ListItemIcon>
                <NavLink to="/contact-us" style={{color:'white',textDecoration:'none'}}>{Drawername}</NavLink>

                </ListItemButton>
            )
            case 'ABOUT-US':
            return(
                <ListItemButton>
                <ListItemIcon style={{color:'white',textDecoration:'none'}}>
                <SendIcon />
                </ListItemIcon>
                <NavLink to="/about-us" style={{color:'white',textDecoration:'none'}}>{Drawername}</NavLink>

                </ListItemButton>
            )
            default:
                return Drawername
    
      }
  }
  
  return (
    <List >{Tabs(Drawername)}
    </List>
  );
}
