import {
  Route,
  Routes,

} from "react-router-dom";
import React, { useState } from "react";
import Home from "./Components/Homepage/Home";
import BridalLehenga from "./Components/Collections/BridalLehenga";
import Contact from "./Components/Homepage/Contact";
import About from "./Components/Homepage/About";
import Consultancy from "./Components/Consultancy/Consultancy";
import Bookappointment from './Components/BookAppointment/Bookappointment'
import MensPage from "./Components/Collections/MensPage";
import WomensPage from "./Components/Collections/WomensPage";
import Profile from "./Components/login/Profile";
import { Context } from "./Context";


function App() {
  const [globaldata,setGlobaldata]=useState('')
  return(
    <>
     <div className='app'>
        <Context.Provider value={{globaldata,setGlobaldata}}>
        <Routes>
          <Route path='/' element={<Home/>}/>
          <Route path='menscollection' element={<MensPage/>}/>
          <Route path='womenscollection' element={<WomensPage/>}/>
          <Route path='bridallehenga' element={<BridalLehenga/>}/>
          <Route path='contact-us' element={<Contact/>}/>
          <Route path='about-us' element={<About/>}/>
          <Route path='consultancy' element={<Consultancy/>}/>
          <Route path='profile' element={ <Profile/>}/>
          <Route path='bookappointment' element={<Bookappointment/>}/>
       </Routes> 
       </Context.Provider>
        
     </div>
     </>
  ) 
}

export default App;
