import * as React from 'react';
import { useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import MobileStepper from '@mui/material/MobileStepper';
import Button from '@mui/material/Button';
import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRight from '@mui/icons-material/KeyboardArrowRight';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';
import Sherwani from '../images/gettyimages-484107954-612x612.jpg'
import Lehenga from '../images/pexels-krishna-studio-2293102.jpg'
import ImageFile1 from '../images/SWAS6411.JPG'
import ImageFile2 from '../images/gettyimages-583763498-612x612.jpg'

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);


const images = [
  {
    imgPath:Sherwani,
  },
  {
   
    imgPath:Lehenga,
  },
  {
   
    imgPath:ImageFile1,
  },
  {
   
    imgPath:ImageFile2,
  },
];

function Swipeable(){
  
 
  const theme = useTheme();
  const [activeStep, setActiveStep] = React.useState(0);
  const maxSteps = images.length;

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStepChange = (step) => {
    
    setActiveStep(step);
  };

  return (
    <Box style={{ position:'absolute',transform:'translate(0,-130px)', maxWidth: '100%', flexGrow: 1,height:'750px' }}>
      
      <AutoPlaySwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={activeStep}
        onChangeIndex={handleStepChange}
        enableMouseEvents
      >
        {images.map((step, index) => (
          <div key={index}>
            {Math.abs(activeStep - index) <= 2 ? (
              <Box
                component="img"
                sx={{
                  height: 750,
                  display: 'block',
                  maxWidth: '100%',
                  overflow: 'hidden',
                  width: '100%'
                }}
                src={step.imgPath}
                alt={step}
              />
            ) : null}
          </div>
        ))}
      </AutoPlaySwipeableViews>
      <MobileStepper
        steps={maxSteps}
        sx={{position:'absolute'}}
        activeStep={activeStep}
        nextButton={
          <Button
            sx={{color:'white',transform:'translateY(-300px)'}}
            size="small"
            onClick={handleNext}
            disabled={activeStep === maxSteps - 1}
          >
            Next
            {theme.direction === 'rtl' ? (
              <KeyboardArrowLeft />
            ) : (
              <KeyboardArrowRight />
            )}
          </Button>
        }
        backButton={
          <Button 
          sx={{color:'white',transform:'translateY(-300px)'}}
          size="small" onClick={handleBack} disabled={activeStep === 0}>
            {theme.direction === 'rtl' ? (
              <KeyboardArrowRight />
            ) : (
              <KeyboardArrowLeft />
            )}
            Back
          </Button>
        }
      />
    </Box>
  );
}

export default Swipeable;


















