import * as React from 'react';
import {  styled, useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import CssBaseline from '@mui/material/CssBaseline';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/DoubleArrow';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import Scrool from './Scrool'
import CollectionTab from './CollectionTab'
import { Stack } from '@mui/system';
import Paper from '@mui/material/Paper';
import DrawerTab from './DrawerTab';

const drawerWidth = 240;

const Main = styled('main', { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open }) => ({
    backgroundColor:'transparent',
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: `-${drawerWidth}px`,
    ...(open && {
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
      marginLeft: 0,
      
    }),
  }),
);

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  transition: theme.transitions.create(['margin', 'width'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: `${drawerWidth}px`,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
  justifyContent: 'flex-end',
}));




 
  

  const Item = styled(Paper)(({ theme }) => ({
    backgroundColor:'transparent',
    color: theme.palette.text.secondary,
    border:'none',
    boxShadow:'none'//boxshadow border none
  }));





export default function PresistentDrawer() {    
  

  const theme = useTheme();
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  }

  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <Scrool>
      <AppBar position="fixed" open={open} color="transparent">
        <Toolbar >
          <IconButton
            
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            sx={{ mr: 2, ...(open && { display: 'none' }),color:'white' }}
          >
            <MenuIcon />
          </IconButton>
          <Box sx={{flexGrow:15}}/>
          <Box sx={{ display: { xs: 'none', md: 'flex' } }} >
          <Stack direction="row" spacing={5}>
           {['HOME','COLLECTION','CONSULTANCY','CONTACT-US','ABOUT-US'].map((text)=>{
            return (
              <Item key={text}>
                <CollectionTab Tabname={text}/>   
           </Item> 
            )
           })}
          </Stack>

          </Box>
         
          
          <Box sx={{ flexGrow: 15 }} />

          <Box sx={{ display: { xs: 'flex', md: 'flex' } ,color:'white'}} >
            <CollectionTab Tabname="SEARCH"/>
          </Box>


          <Box sx={{ flexGrow: 1 }} />
          <Box sx={{ display: { xs: 'flex', md: 'flex'},color:'white' }}>
            
            
              <CollectionTab Tabname="PROFILE" />
           
          </Box>
          








        </Toolbar>
      </AppBar>
      </Scrool>
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: drawerWidth,
            boxSizing: 'border-box',
            backgroundColor:'transparent',  //background transparent
            backdropFilter :'blur(8px)',  //background blur
          },
        }}
        variant="persistent"
        anchor="left"
        open={open}
       
      >
        <DrawerHeader>
          <IconButton sx={{color:'white'}} onClick={handleDrawerClose}>
            {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
          </IconButton>
        </DrawerHeader>
        <Divider  sx={{color:'white'}}/>
        <List>
          {['HOME','COLLECTION','CONSULTANCY','CONTACT-US','ABOUT-US'].map((text) => (
            <DrawerTab key={text} Drawername={text}/>
          ))}
        </List> 
      </Drawer>
      <Main open={open}>
        <DrawerHeader />
      </Main>
    </Box>
  );
}
