import React from 'react'
import Footer from '../Footer/Footer'
import DrawerPersistent from '../navbar/DrawerPresistent'
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';

function About() {

  
  return (
    <div>
     
     <Box sx={{ width: '100%' ,background:'lightgrey'}}>
     <DrawerPersistent/>
     <Box sx={{display:'flex' ,flexGrow:'1',flexDirection:'column'}}>
     <Box sx={{height:'500px',width:'80%',marginInline:'auto',borderRadius:'20px',background:'transparent'}}>
     <Typography variant="h2" gutterBottom sx={{textAlign:'center',fontFamily:'monospace'}}>
        About-us
      </Typography>
      <Box  sx={{height:'400px',width:'80%',marginInline:'auto',overflow:'auto'}}>
        <Typography sx={{textAlign:'center',fontFamily:'monospace',fontSize:'1.5rem',flexWrap:'wrap'}}>
        Diva creation is a Indian based design house which creates exclusive bridal lehenga choli and gown.
        It is founded by Devichand pariyar .
        In 2009,Diva creation began it's journey in manufacturing women's bridal wear.
         Acheived a well known position in competitive couture industry . 
        Diva creation is speciallised in delicate hand embroidery on bridal lehengas and gowns . 
        A wide variety of collection is created  every year to make bride look glorious. 
        A well trained group of team works on garment to satisfy each and every requirement of our customers .
        </Typography>
  

      </Box>
     </Box>
     <Box sx={{height:'600px',width:'80%',marginInline:'auto',background:'transparent',border:'none'}}>
     <Typography variant="h3" gutterBottom sx={{textAlign:'center',fontFamily:'monospace'}}>
      Our Products and brands
      </Typography>
      <Box  sx={{height:'400px',width:'80%',marginInline:'auto',overflow:'auto'}}>
        <Typography sx={{textAlign:'center',fontFamily:'monospace',fontSize:'1.5rem'}}>
        Since 2009, our distinctive brand image has been consistently developed across an expanding number of products,
       price tiers, and markets. Our products, which include bridal collection such as lehenga choli ,indo-western and gowns
       and groom collection such sherwani ,koti kurta , indo-western and jodhpuri  .
       we have been an innovator in aspirational etnic branding and we have had a considerable influence on the way people dress and the way that fashion is advertised and celebrated throughout the world. 
       We combine consumer insights with our design, marketing, and imaging skills to offer.
       
        </Typography>
  

      </Box>
     </Box>
    
    </Box>
   
    </Box>
    <Footer/>
    </div>
  )
}

export default About
