import * as React from 'react';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import Button from '@mui/material/Button';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import {  Navigate, NavLink, useNavigate } from 'react-router-dom';
import SearchIcon from '@mui/icons-material/Search';
import AccountCircle from '@mui/icons-material/AccountCircle';
import Login from '../login/Login';
import Registration from '../login/Registration';
import Cookies from "universal-cookie";



const cookies = new Cookies();





export default function CollectionTab({Tabname}) {
  const navigate=useNavigate()

  const [state, setState] = React.useState({
    top: false,
   
  });


  const toggleDrawer = (anchor, open) => () => {
       switch(Tabname){
        case 'HOME':
          setState({[anchor]:false});
          navigate('/')
          break
        case 'CONTACT-US':
          setState({[anchor]:false});
          navigate('/contact-us')
          break
          case 'ABOUT-US':
          setState({[anchor]:false});
          navigate('/about-us')
          break
          default:
            setState({[anchor]: open});
          
       }
        
        
  };

  //registration 
  const [clicked,setClicked]=React.useState(false)
   
  const isclicked =()=>{
    setClicked(click=>!click)
  }


const list = (anchor)=>{
  switch(Tabname){
    case 'COLLECTION':
    return (
      <Box
      sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 250 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        {['MENS-COLLECTION', 'WOMENS-COLLECTION'].map((text, index) => (
          <ListItem key={text} disablePadding>
            <ListItemButton >
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon  /> : <MailIcon />}
              </ListItemIcon>
                 {text==='MENS-COLLECTION' ? <NavLink to='/menscollection' style={{color:'black',textDecoration:'none'}}>{text}</NavLink> : <NavLink to="/womenscollection"  style={{color:'black',textDecoration:'none'}}>{text}</NavLink>}
            </ListItemButton>
          </ListItem>
        ))}
      </List>    
  
    </Box>
    
    )
    case 'CONSULTANCY':
    return (
      <Box
      sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 250 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        {['Inbox', 'Starred', 'Send email', 'Drafts'].map((text, index) => (
          <ListItem key={text} disablePadding>
            <ListItemButton >
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>      
    </Box>
    )
    case 'SEARCH':
    return (
      <Box
      sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 250 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        {['Inbox', 'Starred', 'Send email', 'Drafts'].map((text, index) => (
          <ListItem key={text} disablePadding>
            <ListItemButton >
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>      
    </Box>
    )
    
    case 'PROFILE':
      const token = cookies.get("TOKEN");
      if(token){
        return <Navigate to='/profile'/>
      }else{
        return (
          <Box
          sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 450 , display:'grid',gridAutoFlow:'column' }}
          role="presentation"
          onClick={toggleDrawer(anchor, true)}
          onKeyDown={toggleDrawer(anchor, true)}
        >
          <Button onClick={isclicked}>{clicked===false? "Sign Up" : "Sign In"}</Button>
        { clicked === false ?  <Login/> : <Registration/>   }
  
                  
        </Box>
        )
        

      }
      
     default:
      return Tabname
  }
}
  return (
    <div>
      {['top'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Button onClick={toggleDrawer(anchor, true)}
          variant="text" color="inherit" sx={{fontSize:'1em',fontWeight:'bold',fontFamily:'Monospace',color:'white'}}
          >{Tabname === 'SEARCH'? <SearchIcon /> : Tabname === 'PROFILE' ?  <AccountCircle/> :Tabname }
          </Button>

          <Drawer
            anchor={anchor}
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
          >
            {list(anchor)}
          </Drawer>
        </React.Fragment>
      ))}
    </div>
  );
}



